.image-tile {
  position: relative;
  height: 100%;
  width: 100%;
}

.image-tile img.contain {
  object-fit: contain;
}

.image-tile img.cover {
  object-fit: cover;
  width: 100%;
  height: 100%;
}

.image-tile p {
  position: absolute;
  width: 100%;
}

.image-tile p.middle {
  top: 50%;
  transform: translateY(-50%);
}

.image-tile p.bottom {
  top: 95%;
  transform: translateY(-5%);
}
